export const UpdateBomitemsMutation = `mutation UpdateBomitemsMutation(
    $bom_items: [bom_items_insert_input!]!,
    $bom_id: Int) {
    insert_bom_versions(
      objects: {
        bom_id: $bom_id,
        bom_items: {data: $bom_items}
      }
    ) {
      affected_rows
    }
  }`;

export const exportMaterialMasterDataAsOne = `mutation exportMaterialMasterDataAsOne($materials: [core_material_master_insert_input!]!) {
  insert_core_material_master(objects: $materials) {
    returning{
      id
      custom_material_id
    }
    affected_rows
  }
}`;

export const CreateProductBomQuery = `mutation CreateProductBomQuery(
  $name: String!,
  $items: [bomItemsArray],
  $product_code_id: Int!,
  ) {
  insert_bom(
    product_code_id: $product_code_id,
    bom_data: {
      items: $items,
      name: $name,
    }) {
    message
  }
}
`;
export const CreateProjectBomQuery = `mutation CreateProjectBomQuery(
  $name: String!,
  $items: [bomItemsArray],
  ) {
  insert_bom(
    bom_data: {
      items: $items,
      name: $name,
    }) {
    message
  }
}`;

export const mappingConfigDataMutation = `mutation mappingConfigDataMutation($tenant_id: Int!, $bom:jsonb!, $material:jsonb!) {
  update_tenant_defaults(
    where: {tenant_id: {_eq: $tenant_id}}
    _set: {bom: $bom, material: $material}
  ) {
    affected_rows
  }
}
`;
