import React from "react";
import { useSelector } from "react-redux";
import { customStyles } from "../../constFunctions/copmponentFunctions";
import Select, { components } from "react-select";
import "./DropDown.css";

function DropDown({ options, handleChange, value, label, noOptionMessage = "No option available", isLoading = true }) {
  const { sideBarOpen, uploadLoader } = useSelector((state) => state.action);

  const filteredOptions = options?.filter((option, index, self) => {
    return index === self?.findIndex((o) => o?.label === option?.label);
  });
  const NoOptionsMessage = (props) => {
    return (
      <components.NoOptionsMessage {...props}>
        <span className="custom-css-class">{noOptionMessage}</span>
      </components.NoOptionsMessage>
    );
  };
  return (
    <div className="react-select-dd">
      {label && <p>{label}</p>}
      <Select
        options={filteredOptions}
        components={{ NoOptionsMessage }}
        styles={customStyles}
        onChange={handleChange}
        value={value}
        className={`${sideBarOpen ? "selectType" : " "}`}
        isDisabled={uploadLoader ? true : false}
        isLoading={isLoading}
      />
    </div>
  );
}

export default DropDown;
